import React, { Component } from 'react';

import './NavigationBar.css';
import NavigationItem from '../Components/NavigationItem.js';

class NavigationBar extends Component {
  constructor(props) {
    super(props);
    this.state={
      'NavItemActive':''
    }
  }
    
  render() {
    return (
      <div className="NavigationBar">
        <NavigationItem item="Home" toLink={process.env.PUBLIC_URL + "/"} />
        <NavigationItem item="Experience" toLink={process.env.PUBLIC_URL + "/experience"} />
        <NavigationItem item="Portfolio" toLink={process.env.PUBLIC_URL + "/portfolio"} />
        <NavigationItem item="Contacts" toLink={process.env.PUBLIC_URL +"/contacts"} />
      </div>
    );
  };
}

export default NavigationBar;